import React from "react";

import { Row, Column } from "components/Grid";

import arrow from "assets/img/icons/orange/link-arrow.svg";
import ExternalLink from "components/learningCenter/category/externalLinks";
import {
  CaptionWrapper,
  Title,
  Body,
  Paragraph,
  FooterContainer,
  FooterItemContainer,
  Image,
  LinkArrow,
  Container,
  ImageTitle,
} from "./elements";

type ArticleProps = [
  {
    metaTitle: string;
    metaDescription: string;
    fields: {
      urlPath: string;
      category: string;
      heroReference: {
        fields: {
          title: string;
          subTitle: string;
          category: string;
          urlPath: string;
        };
      };
      thumbnailImage: {
        fields: {
          file: {
            url: string;
          };
        };
      };
    };
  },
];

type CategoryProps = {
  fields: {
    title: string;
    subTitle: string;
    description: string;
  };
};

interface Props {
  articles: ArticleProps;
  category: CategoryProps;
}

const MosaicValueProps = ({ articles, category }: Props) => (
  <Container>
    <Row padding={30} gutterWidth={26}>
      <Column span={1} spanMedium={1} spanSmall={0} />
      <Column span={10} spanMedium={10} spanSmall={12}>
        <ExternalLink />
      </Column>
      <Column span={1} spanMedium={1} spanSmall={0} />
    </Row>
    <Row padding={30} gutterWidth={26}>
      <Column span={1} spanMedium={1} spanSmall={0} />
      <Column span={4} spanMedium={10} spanSmall={12}>
        <CaptionWrapper>
          <Title>{category.fields.title}</Title>
          <Body>{category.fields.subTitle}</Body>
        </CaptionWrapper>
      </Column>
      <Column span={0} spanMedium={1} spanSmall={0} />
      <Column span={1} spanMedium={1} spanSmall={0} />
      <Column span={5} spanMedium={10} spanSmall={12}>
        <Paragraph>{category.fields.description}</Paragraph>
      </Column>
      <Column span={1} spanMedium={1} spanSmall={0} />
    </Row>
    <Row padding={30} gutterWidth={26}>
      <Column span={1} spanMedium={1} spanSmall={0} />
      <Column span={10} spanMedium={10} spanSmall={12}>
        <FooterContainer>
          {articles.map((article) => (
            <FooterItemContainer
              key={article.fields.urlPath}
              to={
                `learn/${article.fields.category}/${article.fields.urlPath}` ||
                "/"
              }
            >
              <LinkArrow>
                <img src={arrow} alt="arrow" />
              </LinkArrow>
              {article.fields.thumbnailImage &&
                article.fields.thumbnailImage.fields.file && (
                  <Image src={article.fields.thumbnailImage.fields.file.url} />
                )}
              <ImageTitle>
                {article.fields.heroReference.fields.title}
              </ImageTitle>
            </FooterItemContainer>
          ))}
        </FooterContainer>
      </Column>
      <Column span={1} spanMedium={1} spanSmall={0} />
    </Row>
  </Container>
);

export default MosaicValueProps;
