import React from "react";

import Layout from "components/layout";
import SimpleValueProps from "components/learningCenter/category/simpleValueProps";

interface Props {
  location: Location;
  pageContext: {
    contentfulData: [
      {
        metaTitle: string;
        metaDescription: string;
        fields: {
          urlPath: string;
          category: string;
          heroReference: {
            fields: {
              title: string;
              subTitle: string;
              category: string;
              urlPath: string;
            };
          };
          thumbnailImage: {
            fields: {
              file: {
                url: string;
              };
            };
          };
        };
      },
    ];
    categoryData: {
      fields: {
        title: string;
        subTitle: string;
        description: string;
        metaTitle: string;
        metaDescription: string;
      };
    };
  };
  id: string;
  metaTitle?: string;
}

const CategoryLearningCenterTemplate = ({ pageContext, location }: Props) => (
  <Layout
    location={location}
    title={pageContext.categoryData.fields.metaTitle}
    description={pageContext.categoryData.fields.metaDescription}
    whiteContentBackground
    whiteGutterBackground
  >
    <SimpleValueProps
      articles={pageContext.contentfulData}
      category={pageContext.categoryData}
    />
  </Layout>
);
export default CategoryLearningCenterTemplate;
